  --blue: #3eb0ef;
  --green: #a4d037;
  --purple: #ad26b4;
  --yellow: #fecd35;
  --red: #f05230;
  --darkgrey: #15171a;
  --midgrey: #738a94;
  --lightgrey: #c5d2d9;
  --whitegrey: #e5eff5;
  --pink: #fa3a57;
  --brown: #a3821a;
  --darkmode: color(var(--darkgrey) l(+2%));
  --white: #fff; }

/* Ghost文章私有样式 */
/* Preventing full-width image overlap with post image. */
.post-full-image + .post-content .kg-content *:first-child .kg-image {
  width: 100%; }

.site-warp .site-main article .post-content {
