    line-height: 1.5em; }
  .post-content .kg-bookmark-icon {
    width: 18px;
    height: 18px; }
  .post-content .kg-bookmark-thumbnail {
    order: 1;
    min-height: 160px;
    width: 100%; }
  .post-content .kg-bookmark-thumbnail img {
    border-radius: 3px 3px 0 0; }
  .post-content .kg-bookmark-content {
    order: 2; } }

[data-theme='dark'] .site-warp .site-main article .post-content figure.kg-image-card figcaption {
  color: #b7b7b7; }

[data-theme='dark'] .site-warp .site-main article .post-content .kg-card.kg-bookmark-card {
