  [data-theme='dark'] .site-warp .site-main article .post-content .kg-card.kg-bookmark-card .kg-bookmark-container .kg-bookmark-description,
  [data-theme='dark'] .site-warp .site-main article .post-content .kg-card.kg-bookmark-card .kg-bookmark-container .kg-bookmark-metadata {
