  [data-theme='dark'] .site-warp .site-main article .post-content .kg-card.kg-bookmark-card .kg-bookmark-thumbnail:after {
    background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.8));
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

/* 日夜模式支持 */
