/*搜索*/
.app-search-result {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  flex-wrap: wrap;
  z-index: 1030; }
  .app-search-result.active {
    display: flex; }
  .app-search-result .search-close {
    position: absolute;
    z-index: 1031;
    top: 2.5rem;
    right: 2.5rem;
    width: 2.3125rem;
    height: 2.3125rem;
    fill: #ddd;
    cursor: pointer; }
  .app-search-result .search-content {
    flex: 1 0 auto;
    width: 100%;
    max-width: 50rem;
    margin: 2.5rem auto;
    padding: 5rem 1rem; }
    .app-search-result .search-content .search-form {
      position: relative; }
      .app-search-result .search-content .search-form .search-icon {
        position: absolute;
        top: 0.9375rem;
        left: 0;
        font-size: 1.75rem;
        color: #777; }
      .app-search-result .search-content .search-form label {
        width: 100%; }
        .app-search-result .search-content .search-form label .search-input {
          font-size: 1.875rem;
          font-weight: 700;
          display: block;
          width: 100%;
          min-width: 6.25rem;
          height: 3.875rem;
          padding: 0 0 0.1875rem 2.5rem;
          transition: border-color 0.3s ease;
          color: #333;
          border-width: 0 0 0.4375rem 0;
          border-style: solid;
          border-color: #999;
          border-radius: 0;
          outline: 0;
          flex: 0 0 100%;
          background: 0 0; }
    .app-search-result .search-content .search-meta {
      font-size: 0.6875rem;
      font-weight: 700;
      display: block;
      width: 100%;
      margin: 0 auto;
      padding: 0.9375rem 0;
      text-align: left;
      text-transform: uppercase;
      color: #333; }
    .app-search-result .search-content .search-results {
      overflow: auto;
      height: 100vh;
      padding-bottom: 16rem; }
      .app-search-result .search-content .search-results .ghost-search-item {
        margin: 0.9375rem 0 1.875rem 0;
        display: block;
        cursor: pointer; }
        .app-search-result .search-content .search-results .ghost-search-item h2 {
          font-size: 1.875rem;
          color: #333;
          margin-bottom: 0.625rem; }
        .app-search-result .search-content .search-results .ghost-search-item span {
          font-size: 0.8125rem;
          color: #777;
          font-weight: 700; }

[data-theme='dark'] .app-search-result {
  background: #252525; }
  [data-theme='dark'] .app-search-result .search-close {
    fill: #b7b7b7; }
  [data-theme='dark'] .app-search-result .search-content .search-form .search-icon {
